// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store';
import ElementUI from 'element-ui'
import Axios from 'axios'
import VueAxios from 'vue-axios'
// element-uicss
import 'element-ui/lib/theme-chalk/index.css'

import Header from './components/common/Header'//

//js
import utils from '@/utils/util.js'
Vue.prototype.utils=utils

Vue.config.productionTip = false
Vue.use(ElementUI)
//VueAxios,AxiosTypeError
Vue.use(VueAxios,Axios)

Vue.use(Header)//
Vue.component("Header",Header)//

/**
 * 
 */
import {Loading} from 'element-ui'
//axios,methods$axios
Vue.prototype.$Axios=Axios
let loading;
//
let loadingNum=0;
function startLoading() {    
    if(loadingNum==0){
        loading = Loading.service({
          lock: true,
          text: '...',
          background:'rgba(255,255,255,0.5)',
        })
    }
    //1
    loadingNum++;
}
function endLoading() {
    //1
    loadingNum--
    if(loadingNum<=0){
        loading.close()
    }
}
 
//
Axios.interceptors.request.use(request => {
  startLoading();
  return request
}, err => {
  return Promise.reject(err);
});
 
 
//
Axios.interceptors.response.use(response => {
  endLoading();
  return response
}, err => {
  endLoading();
  if (err && err.response) {
    switch (err.response.status) {
      case 400: err.message = '(400)'; break;
      case 401: this.$router.push('/Login'); break;
      case 403: err.message = '(403)'; break;
      case 404: err.message = '(404)'; break;
      case 408: err.message = '(408)'; break;
      case 500: err.message = '(500)'; break;
      case 501: err.message = '(501)'; break;
      case 502: err.message = '(502)'; break;
      case 503: err.message = '(503)'; break;
      case 504: err.message = '(504)'; break;
      case 505: err.message = 'HTTP(505)'; break;
      default: err.message = `(${err.response.status})!`;
    }
  } else {
    err.message = '!'
  }
  message.error(err.message);
  return Promise.reject(err);
});


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App , Header},
  template: '<App/>'
})

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})



