<template>
    <el-menu :default-active="activeIndex2" class="el-menu-demo" mode="horizontal" @select="handleSelect"
        background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
        <el-menu-item index="1" @click="toIndex()"></el-menu-item>
        <el-menu-item index="2" disabled style="cursor: pointer"></el-menu-item>
        <el-menu-item index="3" @click="myDIYBtn(btns)">Hi! {{loginStatus}}
        </el-menu-item>
        <el-menu-item index="4" @click="myDIYBtn_1(btns)">{{loginStatus_1}}</el-menu-item>
        <el-submenu index="5">
            <template slot="title"></template>
            <el-menu-item index="5-1"></el-menu-item>
            <el-menu-item index="5-2"></el-menu-item>
            <!-- <el-menu-item index="5-3">3</el-menu-item>
            <el-submenu index="5-4">
                <template slot="title">4</template>
                <el-menu-item index="5-4-1">1</el-menu-item>
                <el-menu-item index="5-4-2">2</el-menu-item>
                <el-menu-item index="5-4-3">3</el-menu-item>
            </el-submenu> -->
        </el-submenu>
        <el-menu-item index="6"></el-menu-item>
        <el-menu-item index="7"></el-menu-item>
        <el-submenu v-if="this.$store.getters.user!=''" index="8" style="float:right">
            <template slot="title">
                <el-avatar :size="40" :src=this.$store.getters.user.userAvatar />
            </template>
            <el-menu-item index="8-1"></el-menu-item>
            <el-menu-item index="8-2"></el-menu-item>
        </el-submenu>
    </el-menu>
</template>
<script>
export default {
    name: 'Header',
    data() {
        return {
            activeIndex: '1',
            activeIndex2: '1',
            loginStatus: '',
            loginStatus_1: '',
            btns: {
                btnEvent: 'this.$router.push({path:"/Login"})',
                btnEvent_1: 'this.$router.push({path:"/Register"})',
            }

        };
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
            if (key == "5-1") {
                if(this.$store.getters.user!=''){
                    this.$router.push({ path: '/HasTaked' })
                }else{
                    this.$message({
                        type:"error",
                        message:""
                    })
                    this.$router.push({path:"/Login"})
                }
            }
            if (key == "5-2") {
                if(this.$store.getters.user!=''){
                    this.$router.push({ path: '/WalletPage' })
                }else{
                    this.$message({
                        type:"error",
                        message:""
                    })
                    this.$router.push({path:"/Login"})
                }
            }
            if (key == "6") {
                if(this.$store.getters.user!=''){
                    this.$router.push({ path: '/ShoppingCart' })
                }else{
                    this.$message({
                        type:"error",
                        message:""
                    })
                    this.$router.push({path:"/Login"})
                }
            }
            if (key == "8-1") {
                // this.$router.push({ path: '/MyInfo' })
                this.$confirm(',! Do You Know ????', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                })
            }
            if (key == "8-2") {
                this.$router.push({ path: '/Address' })
            }
        },
        myDIYBtn(btn) {
            eval(btn.btnEvent)
            console.log('btn==>' + btn)
        },
        myDIYBtn_1(btn) {
            eval(btn.btnEvent_1)
            console.log('btn==>' + btn)
        },
        toIndex() {
            this.$router.push({ path: '/Index' })
        },
        getStatus() {
            if (this.$store.getters.status === 0) {
                this.loginStatus = ''
                this.loginStatus_1 = ''
            } else {
                this.loginStatus = this.$store.getters.user.netName
                this.loginStatus_1 = ''
                let event = "this.$router.push({path:'/MyCart'})"
                let event_1 = 'this.$router.push({path:"/Login"}),'
                    + 'this.$store.commit("setStatus",0),'
                    + 'this.$store.commit("setMember",""),'
                    + 'this.$store.commit("setUser",""),'
                    + 'this.$store.commit("setWallet","")'

                this.btns.btnEvent = event
                this.btns.btnEvent_1 = event_1
            }
        }
    },
    mounted() {
        this.getStatus()
    }
}
</script>


